.hy-skeleton {
  display: table;
  width: 100%; }
  .hy-skeleton .hy-skeleton-header {
    display: table-cell;
    padding-right: 16px;
    vertical-align: top; }
    .hy-skeleton .hy-skeleton-header .hy-skeleton-avatar {
      background-color: #f2f2f2; }
  .hy-skeleton .hy-skeleton-content {
    display: table-cell;
    vertical-align: top;
    width: 100%; }
    .hy-skeleton .hy-skeleton-content .hy-skeleton-title {
      margin-top: 16px;
      height: 16px;
      width: 100%;
      background: #f2f2f2; }
    .hy-skeleton .hy-skeleton-content .hy-skeleton-graph {
      margin: 0;
      padding: 0;
      margin-top: 24px;
      list-style: none; }
      .hy-skeleton .hy-skeleton-content .hy-skeleton-graph li {
        height: 16px;
        background: #f2f2f2;
        list-style: none;
        width: 100%;
        margin-top: 16px; }
  .hy-skeleton.hy-skeleton-active .hy-skeleton-avatar,
  .hy-skeleton.hy-skeleton-active .hy-skeleton-title,
  .hy-skeleton.hy-skeleton-active .hy-skeleton-graph > li {
    background: linear-gradient(90deg, #f2f2f2 25%, #e6e6e6 37%, #f2f2f2 63%);
    -webkit-animation: skeleton-loading 1.4s ease infinite;
    animation: skeleton-loading 1.4s ease infinite;
    background-size: 400% 100%; }

@keyframes skeleton-loading {
  0% {
    background-position: 100% 50%; }
  100% {
    background-position: 0 50%; } }
